*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1, h2, h3 {
  font-family: "Josefin Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 7rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
  line-height: 1.8;
}

.tagline {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 900;
  color: #171820;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: image-set(url(../img/fondo-header.avif) 1x, url(../img/fondo-header.webp) 1x, url(../img/fondo-header.jpg) 1x);
  background-size: cover;
  background-repeat: no-repeat;
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6); /* Color negro semitransparente para el oscurecimiento */
}
.header__nav {
  width: 100%;
  display: flex;
  backdrop-filter: blur(1rem);
  box-shadow: 0.3rem 0.3rem 0.5rem rgba(0, 0, 0, 0.5);
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__nav {
    height: 7rem;
    justify-content: flex-end;
  }
}
.header__enlaces {
  padding: 2rem;
  font-size: 2rem;
}
.header__enlaces:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: #df861d;
  text-shadow: 0.3rem 0.3rem 0.5rem rgb(0, 0, 0);
}
.header__contenedor {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70vw;
}
.header__titule, .header__name, .header__iam, .header__enlaces {
  position: relative;
  z-index: 2; /* Coloca el texto por encima del filtro */
  color: #FFFFFF;
  text-shadow: 0.3rem 0.3rem 0.5rem rgba(0, 0, 0, 0.6);
}
@media (min-width: 768px) {
  .header__titule {
    font-size: 6rem;
  }
}
@media (min-width: 768px) {
  .header__name {
    font-size: 8rem;
  }
}
.header__iam {
  text-align: center;
}
@media (min-width: 768px) {
  .header__iam {
    font-size: 2.2rem;
  }
}
.header__btn {
  position: relative;
  z-index: 2; /* Coloca el boton por encima del filtro */
  margin-top: 10rem;
  background-color: #FFFFFF;
  padding: 1.2rem 3.5rem;
  color: #000;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color, border-radius, color, text-shadow;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .header__btn {
    width: auto;
  }
}
.header__btn:hover {
  border-radius: 0.8rem;
  background-color: #cccccc;
  color: #FFFFFF;
  cursor: pointer;
  text-shadow: 0.3rem 0.3rem 0.5rem rgba(0, 0, 0, 0.6);
}
.header__btn:hover {
  background-color: #df861d;
  box-shadow: 0.3rem 0.3rem 0.5rem rgba(0, 0, 0, 0.5);
  text-shadow: 0.3rem 0.3rem 0.5rem rgba(0, 0, 0, 0.6);
}